.form-control {
  margin: 0 auto 0 auto;
  max-width: 400px;
}

.btn-submit {
  max-width: 200px;
  margin: 0 auto 0 auto;
}

.article-section-title {
  line-height: 3em;
}

.text-area {
  height: 125px;
  resize: none;
}

.confirmation-message {
  height:55px;
  font-size: 14px;
  margin: 0 auto;
}

.info-scroll {
  left: 0;
  right: 0;
  position: absolute;
  bottom: 0;
  opacity: 0;
  -webkit-animation: fade-out 5s;
          animation: fade-out 5s;
}

.message-scroll {
  position: relative;
}

.last-message {
  position: relative;
}

@-webkit-keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


